<template>
    <div>
        <hi id="hello">임시페이지입니다</hi>
    </div>
</template>

<script>
export default {

      // http통신을 위한 설치

  created() {
      this.$http.get('/api/login')
          .then((res) => {
              const user = res.data.user;
              if (user) this.user = user;
          })
          .catch((err) => {
              console.error(err);
          });
  }
    
}
</script>

<style>
#hello{
    position:fixed;
    top:250px; 
    left:700px; 
    width:auto; 
    height:100px;
    padding:40px 0;
}
</style>