<template>
    <div>
    </div>
</template>

<script>
export default {
      // http통신을 위한 설치

  created() {
      this.$http.get('/api/login')
          .then((res) => {
              const user = res.data.user;
              if (user) this.user = user;
          })
          .catch((err) => {
              console.error(err);
          });
  }
}
</script>

